#seeker-employer-section {
    .seeker-employer-wrapper {
        .seeker-employer-container {
            margin-bottom: 40px;
            @include breakpoint ($large) {
                margin-bottom: 90px;
            }
            .seeker-employer {
                h2 {
                    text-align: center;
                    font-size: 3em;
                    color: white;
                    background-color: $blue;
                    padding: 30px 0;
                    padding-top: 40px;
                    font-weight: 100;
                    margin: 0;
                    letter-spacing: 0px;
                    position: relative;
                    @include font-smoothing;                    
                    span {
                        font-weight: 600;
                    }
                    &:after {
                        top: 100%;
                        left: 50%;
                        border: solid transparent;
                        content: " ";
                        height: 0;
                        width: 0;
                        position: absolute;
                        pointer-events: none;
                        border-color: rgba(37, 83, 128, 0);
                        border-top-color: #255380;
                        border-width: 15px;
                        margin-left: -15px;
                    }
                }
                .tab-content-container {
                    width: 88%;
                    margin: 0 auto;
                    @include clearfix;
                    #seekers {
                        background-color: $blue;
                        color: white;
                        transition: color 0.4s ease;
                    }
                    #employers {
                        color: $blue;
                        transition: color 0.4s ease;
                    }
                    .tabs {
                        padding: 40px 0;
                        text-align: center;
                        padding-bottom: 0;
                        @include clearfix;
                        button {
                            width: 50%;
                            float: left;
                            margin: 0;
                            padding: 0;
                            border: 1px solid $blue;
                            &:nth-child(2) {
                                border-left: 0;
                            }
                            a {
                                padding: 15px 0px;
                                padding-top: 22px;
                                display: block;
                            }
                        }
                        @include breakpoint ($large) {
                            width: 48%;
                            margin: 0 auto;
                            max-width: 400px;
                        }
                        @include breakpoint ($large) {
                            margin-top: 60px;
                        }
                    }
                    .get-started-content-container {
                        @include clearfix;
                        @include breakpoint ($large) {
                            width: 75%;
                            max-width: 800px;
                            margin: 0 auto;
                        }
                        .tab-content-wrapper {
                            #employer-tab-content {
                                // display: none;
                            }
                            .tab-content {
                                h1 {
                                    font-size: 2em;
                                    color: $blue;
                                    text-align: center;
                                    margin: 0;
                                    padding: 25px;
                                    padding-top: 33px;
                                    @include breakpoint ($large) {
                                        text-align: left;
                                        padding-left: 0;
                                        padding-bottom: 10px;
                                        padding-top: 50px;
                                    }
                                }
                                p {
                                    text-align: center;
                                    color: $txt-grey;
                                    font-size: 20px;
                                    line-height: 28px;
                                    font-weight: 200;
                                    @include breakpoint ($large) {
                                        text-align: left;
                                        font-size: 16px;
                                        line-height: 22px;
                                    }
                                }
                                a {
                                    padding: 23px 0 15px 0;
                                    text-align: center;
                                    margin-top: 20px;
                                    font-size: 1.5em;
                                    color: $blue;
                                    background-color: $green;
                                    display: block;
                                    transition: 0.2s ease;
                                    @include breakpoint ($large) {
                                        &:hover {
                                            background-color: $blue;
                                            color: $green;
                                        }
                                    }
                                }
                            }
                            @include breakpoint ($large) {
                                width: 48%;
                                float: right;
                            }
                        }
                        #job-tab-content {
                            // display: block;
                            transition: opacity 0.5s ease;
                        }
                        #employer-tab-content {
                            transition: opacity 0.5s ease;
                        }
                        #photo-container {
                            transition: opacity 0.5s ease;
                        }
                        .photo-container {
                            display: none;
                            transition: opacity 0.5s ease;
                            @include breakpoint ($large) {
                                display: block;
                                width: 48%;
                                float: left;
                                height: 310px;
                                background: url(../img/img-job-seekers-form.jpg) 0% 50% no-repeat;
                                background-size: contain;
                                margin-top: 55px;
                            }
                        }  
                        .photo-container2 {
                            display: none;
                            transition: opacity 0.5s ease;
                            @include breakpoint ($large) {
                                display: block;
                                width: 48%;
                                float: left;
                                height: 310px;
                                background: url(../img/img-employers-form.jpg) 0% 50% no-repeat;
                                background-size: contain;
                                margin-top: 55px;
                            }
                        }
                    }
                }
            }
        }
    }
}

#awards-section {
    .awards-wrapper {
        padding: 40px 0;
        background-color: #f3f3f3;
        h1 {
            text-align: center;
            color: $blue;
        }
    }
    .flexslider {
        background: #f3f3f3;
        border: none;
        .slides {
            @include breakpoint ($large) {
                width: 1080px !important;
                margin: 0 auto;
            }
            li {
                border: 10px solid #e1e1e1;
                max-width: 350px;
            }
        }
    }
    .flex-control-nav {
        li {
            border: 0;
        }
    }
}

#employer-tab-content {
    label {
        display: none;
    }
    li {
        list-style-type: none;
    }
    input {
        padding: 5px;
        padding-top: 10px;
        border: 1px solid #d1d1d1;
        margin: 0;
        width: 100%;
        margin-bottom: 5px;
    }
    ul {
        padding: 0;
        margin-bottom: 0px;
        margin-top: 10px;
    }
    textarea {
        width: 100%;
        height: 50px;
        padding: 5px;
        border: 1px solid #d1d1d1;
    }
    #gform_submit_button_1 {
        padding: 23px 0 15px 0;
        text-align: center;
        font-size: 1.5em;
        color: $blue;
        background-color: $green;
        display: block;
        border: none;
        transition: 0.2s ease;
        @include breakpoint ($large) {
            &:hover {
                background-color: $blue;
                color: $green;
            }
        }
    }
}