#hero-section {
    .hero-wrapper {
        .hero-container {
            background: url(../img/bg-main-marketing.jpg) 50% 10% no-repeat;
            background-size: cover;
            @include breakpoint ($large) {
                background-attachment: fixed;
            }
            .hero-content {
                padding: 70px 0;
                width: 90%;
                margin: 0 auto;
                @include breakpoint ($medium) {
                    width: 550px;
                    padding-bottom: 100px;
                }
                h1 {
                    font-size: 22px;
                    text-align: center;
                    font-weight: 100;
                    color: white;
                    text-shadow: 1px 1px 2px rgba(0,0,0,.5);
                    font-weight: 200;
                    span {
                        font-weight: 800;
                        font-size: 28px;
                    }

                    @include breakpoint ($medium) {
                        span {
                            font-size: 2.4em;
                            letter-spacing: 1px;
                            line-height: 1.4;
                        }
                        &:nth-child(2) {
                            width: 80%;
                        }
                    }
                }
                @include breakpoint ($large) {
                    width: 940px;
                    padding: 200px 0;
                    h1 {
                        font-size: 35px;
                        span {
                            font-size: 2.7em;
                        }
                    }
                }
            }
        }
    }
}

#big-box-links-area {
    .big-box-links-wrapper {
        .big-box-links-container {
            @include breakpoint ($medium) {
                width: 100%;
                max-width: 900px;
                margin: 0 auto;
                @include clearfix;
                margin-top: -60px;
            }
            .box {
                padding-bottom: 50px;
                @include breakpoint ($medium) {
                    width: 50%;
                    float: left;
                }
                h2 {
                    margin: 0;
                    padding: 20px 0;
                    text-align: center;
                    padding-top: 40px;
                    width: 281px;
                    margin: 0 auto;
                    font-weight: 200;
                    font-size: 35px;
                    line-height: 1;
                    letter-spacing: 1px;
                    span {
                        font-size: 1.8em;
                        font-weight: 800;
                    }
                    @include breakpoint ($medium) {
                        padding-top: 85px;
                        padding-bottom: 75px;
                    }
                    @include breakpoint ($large) {
                        width: 280px;
                        margin: 0 auto;
                        padding-bottom: 15px;
                        padding-top: 50px;
                        font-weight: 200;
                        line-height: 1;
                        font-size: 45px;
                        span {
                            font-size: 1.4em;
                            font-weight: 800;
                        }
                    }
                }
            }
            .box-1 {
                background-color: $blue;
                color: white;
                transition: 0.2s ease;
                @include breakpoint ($medium) {
                    background-color: rgba(25, 74, 121, .9);
                }
                @include breakpoint ($large) {
                    &:hover {
                        background-color: rgba(25, 74, 121, 1);       
                    }
                }
                .arrow-wrap {
                    background-color: white;
                    width: 120px;
                    margin: 0 auto;
                    // margin-bottom: 50px;
                    padding: 20px;
                    border-radius: 50%;
                    .arrow-contain {
                        background-image: linear-gradient(transparent, transparent),url(../img/arrow-blue.svg);
                    }
                }
            }
            .box-2 {
                color: $blue;
                background-color: #f3f3f3;
                transition: 0.2s ease;
                @include breakpoint ($medium) {
                    background-color: rgba(220, 220, 220, .6);
                }
                @include breakpoint ($large) {
                    &:hover {
                        background-color: rgba(220, 220, 220, 1);
                    }
                }
                .arrow-wrap {
                    background-color: $blue;
                    width: 120px;
                    margin: 0 auto;
                    // margin-bottom: 50px;
                    padding: 20px;
                    border-radius: 50%;
                    .arrow-contain {
                        background-image: linear-gradient(transparent, transparent),url(../img/arrow-white.svg);
                    }
                }
            }
            .arrow-wrap {
                    .arrow-contain {
                        background-size: contain;
                        background-repeat: no-repeat;
                            width: 0px;
                        margin: 0 auto;
                        border-radius: 50%;
                        padding: 40px;
                        @include breakpoint ($large) {
                            padding: 40px;
                        }
                    }
                    @include breakpoint ($large) {
                        margin-bottom: 50px;
                    }
                }
        }
    }
}

#spacer {
    display: none;
    @include breakpoint ($large) {
        display: block;
        width: 100%;
        padding-top: 119px;
    }
}

#green-line {
    height: 10px;
    border-top: 10px solid rgba(167,245,136,.5);
    width: 80%;
    z-index: 1;
    position: absolute;
    top: 270px;
    @include breakpoint ($medium) {
        top: 250px;
    }
    @include breakpoint ($large) {
        top: 500px;
    }
}