#white-banner-section {
    .white-banner-wrapper {
        .white-banner-container {
            .white-banner {
                padding: 50px 0;
                width: 90%;
                margin: 0 auto;
                .white-banner-txt {
                    h1 {
                        font-size: 1.5em;
                        line-height: 1.2;
                        margin-bottom: 10px;
                        padding-bottom: 10px;
                        border-bottom: 5px solid $green;
                        color: $blue;
                        display: inline-block;
                    }
                    p {
                        color: $txt-grey;
                        font-size: 16px;
                        font-weight: 200;
                        padding-bottom: 20px;
                    }
                    a {
                        display: block;
                        padding: 20px 0;
                        padding-bottom: 13px;
                        background-color: $blue;
                        color: white;
                        text-align: center;
                        font-size: 20px;
                        margin-bottom: 20px;
                        transition: 0.2s ease;
                        border: 2px solid white;
                        max-width: 300px;
                        @include breakpoint ($large) {
                            &:hover {
                                background-color: white;
                                color: $blue;
                                border: 2px solid $blue;
                            }
                        }
                    }
                    ul {
                        li {
                            color: $txt-grey;
                            font-weight: 100;
                        }
                    }
                }
                img {
                    text-align: center;
                    border-left: 30px solid $green;
                }
                @include breakpoint ($medium) {
                    @include clearfix;
                    max-width: 1024px;
                    .white-banner-txt {
                        width: 68%;
                        float: left;
                    }
                    .white-banner-img {
                        width: 28%;
                        float: right;
                        padding-top: 20px;
                    }
                }
                @include breakpoint ($large) {
                    .white-banner-img {
                        padding: 0;
                    }
                    .white-banner-txt {
                        padding-top: 30px;
                    }
                }
            }
        }
    }
}

