#job-apply-banner {
    padding: 50px 0;
    padding-bottom: 100px;
.job-apply-wrapper {
    @include clearfix;
    width: 80%;
    margin: 0 auto;
    max-width: 900px;
    .tab-content-wrapper {
            .tab-content {
                margin-top: 30px;
                h1 {
                    font-size: 2em;
                    color: $blue;
                    text-align: center;
                    margin: 0;
                    padding: 25px;
                    padding-top: 33px;
                    @include breakpoint ($large) {
                        text-align: left;
                        padding-left: 0;
                        padding-bottom: 10px;
                        padding-top: 50px;
                    }
                }
                p {
                    text-align: center;
                    color: $txt-grey;
                    font-size: 20px;
                    line-height: 28px;
                    font-weight: 200;
                    @include breakpoint ($large) {
                        text-align: left;
                        font-size: 16px;
                        line-height: 22px;
                    }
                }
                a {
                    padding: 23px 0 15px 0;
                    text-align: center;
                    margin-top: 20px;
                    font-size: 1.5em;
                    color: $blue;
                    background-color: $green;
                    display: block;
                    transition: 0.2s ease;
                    @include breakpoint ($large) {
                        &:hover {
                            background-color: $blue;
                            color: $green;
                        }
                    }
                }
            }
            @include breakpoint ($large) {
                width: 48%;
                float: right;
            }
        } 
        .photo-container {
            display: none;
            transition: opacity 0.5s ease;
            @include breakpoint ($large) {
                display: block;
                width: 48%;
                float: left;
                height: 310px;
                background: url(../img/img-job-seekers-form.jpg) 0% 50% no-repeat;
                background-size: contain;
                margin-top: 50px;
            }
        }
    }
}


