#site-header {
    @include breakpoint ($large) {
        position: relative;
    }
    .upper-nav-contain {
            max-width: 1275px;
        .upper-nav {
            display: none;
            right: 12%;
            @include breakpoint ($large) {
                display: block;
                position: absolute;
                ul {
                    padding: 0;
                    margin: 0;
                    li {
                        padding: 0;
                        margin: 0;
                        list-style-type: none;
                        display: inline-block;
                        width: 110px;
                        margin-right: -5px;
                        border-right: 2px solid rgba(255,255,255,.5);
                        background-color: $blue;
                        &:hover {
                            background-color: #003b54;
                        }
                        &:nth-child(3) {
                            margin-right: 0;
                            border-right: 0;
                        }
                        a {
                            font-size: 12px;
                            color: white;
                            display: block;
                            padding: 9px 0;
                            padding-top: 13px;
                            text-align: center;
                            transition: 0.2s ease;
                        }
                    }
                }
            }
        }
    }
    .nav-bar {
        box-shadow: 0px 0px 3px 0px rgba(0,0,0,0.2);
        position: relative;
        @include breakpoint ($large) {
            position: absolute;
            width: 100%;
            background-color: rgba(255,255,255,.8);
            top: 40px;
        }
        .logo-bar-wrap {
            position: relative;
            width: 83%;
            margin: 0 auto;
            @include breakpoint ($large) {
                width: 95%;
                max-width: 1024px;
            }
            .logo-bar-container {
                width: 90%;
                .logo-bar {
                    padding: 20px 0;
                    .logo {
                        background-image: linear-gradient(transparent, transparent),url(../img/logo.svg);
                        background-size: contain;
                        background-repeat: no-repeat;
                        margin: 0;
                        line-height: 39px;
                        a {
                            display: block;
                            text-indent: -9999px;
                        }
                    }
                }
            }
            .menuToggle {
                position: absolute;
                top: 28px;
                right: -9px;
                padding: 5px 10px;
                border-radius: 5px;
                font-size: 0.5em;
                letter-spacing: 1px;
                text-transform: uppercase;
                @include breakpoint ($large) {
                    display: none;
                }

                .hamburger {
                    position: relative;
                    display: inline-block;
                    width: 3em;
                    height: 2.1em;
                    border-top: 0.4em solid $blue;
                    border-bottom: 0.4em solid $blue;
                }

                .hamburger:before {
                    content: "";
                    position: absolute;
                    top: 0.5em;
                    left: 0px;
                    width: 100%;
                    border-top: 0.4em solid $blue;
                }
            }
        }
        .site-navigation {
            display: none;
            position: absolute;
            width: 100%;
            z-index: 4;
            background-color: rgba(0,103,170,.95);
            @include breakpoint ($large) {
                display: block;
                background-color: transparent;
            }
            @include clearfix;
            .nav-contain {
                @include clearfix;
                @include breakpoint ($large) {
                    text-align: center;
                }
                .mainNavigation {
                    padding: 10px 10px;
                    margin-bottom: 10px;
                    @include breakpoint ($large) {
                        padding: 0;
                        margin: 0;
                        width: 75%;
                        float: right;
                        margin-top: -79px;
                        margin-bottom: 0;
                    }
                    li {
                        padding: 0;
                        list-style-type: none;
                        width: 90%;
                        margin: 0 auto;
                        ul {
                            display: none;
                            padding: 0;
                            li {
                                width: 100%;
                                a {
                                    width: 90%;
                                    margin: 0 auto;
                                }
                                &:last-child {
                                    padding-top: 0;
                                }
                            }
                        }
                        @include breakpoint ($large) {
                            > ul {
                                position: absolute;
                                opacity: 0;
                                display: block;
                                border: 0;
                                clip: rect(0 0 0 0);
                                height: 1px;
                                margin: -1px;
                                overflow: hidden;
                                padding: 0;
                                position: absolute;
                                width: 1px;
                            }
                            position: relative;
                            &:hover {
                                a {
                                    background-color: white;
                                    transition: 0.2s ease;
                                }
                                > ul {
                                    transition: opacity 0.7s ease;
                                    background-color: white;
                                    width: 140%;
                                    border-bottom: 5px solid $blue;
                                    opacity: 1;
                                    clip: auto;
                                    height: auto;
                                    margin: 0;
                                    overflow: visible;
                                    position: absolute;
                                    li {
                                        a {
                                            text-align: left;
                                            padding: 20px 10px;
                                            padding-top: 27px;
                                            border-bottom: 1px solid $txt-grey;
                                            width: 72%;
                                        }
                                        &:last-child {
                                            a {
                                                border-bottom: 0;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                        .hasChildren {
                            border-bottom: 5px solid transparent;
                            @include breakpoint ($large) {
                                border-bottom: none;
                            }
                        }
                        .childrenborder {
                            border-bottom: 5px solid $blue;
                            transition: 0.3s ease;
                        }
                        .plus {
                            display: block;
                            width: 30px;
                            height: 30px;
                            background: url(../img/plus.png) no-repeat 0 0;
                            background-image: -webkit-linear-gradient(transparent, transparent),url(../img/plus.svg);
                            background-image: linear-gradient(transparent, transparent),url(../img/plus.svg);
                            position: absolute;
                            right: 20px;
                            margin-top: 10px;
                        }
                        .plusOpen {
                            background-position: 0 -30px;
                        }
                        @include breakpoint ($large) {
                            margin: 0;
                            padding: 0;
                            width: auto;
                            display: inline-block;
                            .plus {
                                display: none;
                            }
                        }
                        a {
                            display: block;
                            width: 100%;
                            color: white;
                            padding: 15px 27px;
                            font-size: 18px;
                            @include breakpoint ($large) {
                                color: $txt-grey;
                                padding: 25px 27px;
                                padding-top: 33px;
                                font-size: 14px;
                            }
                        }
                        &:nth-child(6) {
                            a {
                                border-bottom: 0;
                            }
                            @include breakpoint ($large) {
                                // margin-top: 20px;
                            }
                        }
                        &:last-child {
                            border-bottom: 5px solid $blue;
                            @include breakpoint ($large) {
                                border-bottom: 0;
                            }
                        }
                    }
                }
                #mobile-blue-links {
                    padding: 0 10px;
                    @include breakpoint ($large) {
                        display: none;
                    }
                    li {
                        padding: 0;
                        list-style-type: none;
                        width: 90%;
                        margin: 0 auto;
                        a {
                            display: block;
                            width: 100%;
                            color: $super-light-blue;
                            padding: 15px 0;
                            font-size: 18px;
                            // border-bottom: 1px solid rgba(255,255,255, .2);
                        }
                    }
                }
            }
            .sub-menu {
                li {
                    &:hover {
                        a {
                            color: #0067aa;
                        }
                    }
                }
            }
        }
    }
}

#logo-cover {
    @include breakpoint ($large) {
        display: block;
        position: absolute;
        padding-top: 79px;
        width: 260px;
        top: 0px;
        left: -15px;
        z-index: 100000;
    }
}