/* ==========================================================================
	Variables
========================================================================== */

// Fonts
$sans-serif:  "Nimbus Sans", sans-serif;
$serif:       "Times New Roman", serif;

// Colors
$blue:                #255380;
$light-blue:          #0067aa;
$super-light-blue:    #0fffff;
$faded-bg:            #f3f6f8;
$txt-grey:            #888888;
$green:               #b9f7a0;
$orange:              #e5562f;

// Color Assignments
$link-color:  $blue;

// Media Query Breakpoints
$xsmall:      20em;  // 320px
$small:       30em;  // 480px
$medium:      48em;  // 768px
$large:       64em;  // 1024px
$xlarge:      80em;  // 1280px