#footer {
    .footer-wrapper {
        border-top: 1px solid #d1d2d6;
        .footer-container {
            width: 85%;
            max-width: 1024px;
            margin: 0 auto;
            @include breakpoint ($large) {
                width: 95%; 
            }
            .footer-content {
                text-align: center;
                @include breakpoint ($large) {
                    @include clearfix;
                    padding: 50px 0;
                }
                .footer-logo-container {
                    padding: 25px 0;
                    border-bottom: 1px solid #d1d2d6;
                    padding-bottom: 15px;
                }
                .social {
                    padding: 15px 0;
                    .copyright {
                        padding: 15px 0 0 0;
                        p {
                            font-size: 12px;
                            color: $txt-grey;
                        }
                        a {
                            font-weight: 600;
                            color: $blue;
                            font-size: 12px;
                            transition: 0.2s ease;
                            @include breakpoint ($large) {
                                &:hover {
                                    color: $light-blue;;
                                }
                            }
                        }
                    }
                    .social-icons {
                        ul {
                            padding: 0;
                            margin-bottom: 0px;
                            li {
                                list-style-type: none;
                                display: inline-block;
                                margin-right: 5px;
                                &:last-child {
                                    margin-right: 0;
                                }
                                a {
                                    width: 25px;
                                    height: 25px;
                                    svg {
                                        width: 25px;
                                        height: 25px;
                                    }  
                                }
                            }
                        }
                    }
                }
                .location-box {
                    padding: 20px 0;
                    border-top: 1px solid #d1d2d6;
                    &:first-child {
                        padding-top: 0;
                        padding-bottom: 0;
                        border-top: 0;
                    }
                    @include breakpoint ($large) {
                        width: 25%;
                        float: left;
                        border-top: none;
                        border-right: 1px solid #d1d2d6;
                        &:last-child {
                            border-right: none;
                        }
                        padding: 0 25px;
                        .footer-logo-container {
                            svg#Layer_1 {
                                width: 90%;
                            }
                        }
                    }
                    .location {
                        h2 {
                            color: $blue;
                            margin-top: 0;
                        }
                        ul {
                            padding: 0;
                            li {
                                padding: 0;
                                margin: 0;
                                color: $txt-grey;
                                list-style-type: none;
                                font-size: 14px;
                                line-height: 18px;
                            }
                        }
                        a {
                            font-weight: 800;
                            padding-bottom: 5px;
                            border-bottom: 10px solid $super-light-blue;
                            transition: 0.2s ease;
                            color: #0067aa;
                            @include breakpoint ($large) {
                                &:hover {
                                    border-bottom: 10px solid $green;
                                }
                            }
                        }
                        margin-bottom :15px;
                    }
                }
            }
        }
    }
}