#divider {
    display: none;
    @include breakpoint ($large) {
        display: block;
        padding-top: 50px;
    }
}

#main-content-section {
    max-width: 2000px;
    margin: 0 auto;
    .main-content-wrapper {
        .main-content-container {
            .main-content {
                text-align: center;
                width: 90%;
                margin: 0 auto;
                padding: 50px 0;
                h1 {
                    font-size: 5em;
                    color: $blue;
                    font-weight: 800;
                    margin: 0;
                    height: 80px;
                    line-height: 85px;
                }
                h3 {
                    color: $txt-grey;
                    font-weight: 200;
                    font-size: 1.8em;
                    margin: 0;
                    margin-bottom: 20px;
                }
                p {
                    color: $txt-grey;
                    font-size: 20px;
                    line-height: 28px;
                    font-weight: 200;
                }
                a{
                    display: none;
                    @include breakpoint ($large) {
                        display: block;
                        @include clearfix;
                        text-align: center;
                        width: 275px;
                        margin: 0 auto;
                        margin-top: 50px;
                        margin-bottom: 75px;
                        .why-link {
                            float: left;
                            transition: 0.2s ease;
                        }
                        .why-link-txt {
                            width: 74%;
                            padding: 20px 0;
                            padding-bottom: 13px;
                            font-size: 1.2em;
                            font-weight: 800;
                            border: 10px solid rgba(221, 231, 242, 1);
                            letter-spacing: 1px;
                        }
                        .why-arrow-container {
                            width: 25.7%;
                            border: 10px solid rgba(221, 231, 242, 1);
                            border-left: 0;
                            .why-arrow {
                                padding: 10px;
                            }
                        }
                    }
                    &:hover {
                        .why-link {
                            // background-color: #f0f0f0;
                        }
                        .why-link-txt {
                            border: 10px solid rgba(58, 100, 140, .4);
                        }
                        .why-arrow-container {
                            border: 10px solid rgba(58, 100, 140, .4);
                            border-left: 0;
                        }
                    }
                }
                @include breakpoint ($large) {
                    width: 60%;
                    margin: 0 20%;
                    max-width: 598px;
                    margin-top: -50px;
                    padding: 105px 0;
                    h1 {
                        font-size: 8em;
                        line-height: 50px;
                    }
                    h3 {
                        margin-bottom: 5px;
                        font-size: 56px;
                        letter-spacing: -1.5px;
                    }
                    p {
                        font-size: 17.5px;
                        line-height: 1.2;
                        color: $blue;
                    }
                }
            }
            a {
                display: block;
                @include clearfix;
                .mobile {
                    float: left;
                }
                @include breakpoint ($large) {
                    display: none;
                }
            }
            .mobile-learn-btn {
                display: block;
                width: 100%;
                background-color: $faded-bg;
                font-size: 1.8em;
                font-weight: 800;
                color: $blue;
                text-align: center;
                padding-bottom: 20px;
                padding-top: 28px;
                @include breakpoint ($large) {
                    display: none;
                }
            }
        }
        @include breakpoint ($large) {
            background: url(../img/why-recruiting-solutions.png) 75% 50% no-repeat;
            background-size: contain;;
            padding: 60px 0;
            max-height: 650px;
        }
    }
    #XMLID_1_, #XMLID_5_{
        fill: #A7F588;
    }
}


@media screen and (max-width: 1023px) {
    #grey-border {
        display: none;
    }
 }