#staff-squares-section {
    .staff-squares-wrapper {
        .staff-squares-container {
            .staff-squares {
                margin-bottom: 75px;
                .square-list {
                    padding: 0;
                    margin: 0;
                    @include clearfix;
                    .square {
                        list-style-type: none;
                        text-align: center;
                        width: 50%;
                        float: left;
                        .square-image {
                            background-size: cover;
                            height: 149px;
                            .hover-contain {
                                background-color: rgba(0,74,106,.5);
                                transition: opacity 0.2s ease;
                                opacity: 1;
                                .square-hover {
                                    width: 90%;
                                    margin: 0 auto;
                                    .name-title {
                                        border-bottom: 5px solid $light-blue;
                                        margin-bottom: 12px;
                                        h3 {
                                            margin: 0;
                                            padding: 30px 0;
                                            padding-bottom: 0px;
                                            font-size: 14px;
                                            text-transform: uppercase;
                                            color: white;
                                        }
                                        h4 {
                                            margin: 0;
                                            padding-top: 5px;
                                            font-size: 12px;
                                            padding-bottom: 15px;
                                            color: white;
                                        }
                                    }
                                    a {
                                        display: block;
                                        font-size: 14px;
                                        letter-spacing: 1px;
                                        text-transform: uppercase;
                                        padding-bottom: 25px;
                                        color: $green;
                                        font-weight: 600;
                                    }
                                }
                                @include breakpoint ($medium) {
                                    height: 189px;
                                    margin-top: -20px;
                                    padding-top: 20px;
                                }
                            }
                            @include breakpoint ($medium) {
                                padding: 20px 0;
                                height: 189px;
                            }
                            @include breakpoint ($large) {
                                opacity: 1;
                                .hover-contain {
                                    opacity: 0;
                                }
                                &:hover {
                                    .hover-contain {
                                        opacity: 1;
                                    }
                                }
                            }
                        }
                        @media screen and (min-width: 600px) {
                            width: 33.3333%;
                        }
                        @include breakpoint ($medium) {
                            width: 25%;
                        }
                        @include breakpoint ($large) {
                            width: 16.6666%;
                        }
                    }
                    @include breakpoint ($large) {
                        max-width: 1400px;
                        margin: 0 auto;
                    }
                }
            }
        }
    }
}