#blog-archive-section {
    .blog-archive-wrapper {
        .blog-archive-container {
            .blog-archive {
                width: 90%;
                margin: 0 auto;
                padding: 20px 0;
                .post {
                    border-bottom: 1px solid $txt-grey;
                    h1 {
                        font-size: 1.5em;
                        line-height: 1.2;
                        margin-bottom: 10px;
                        padding-bottom: 10px;
                        border-bottom: 5px solid $green;
                        color: $blue;
                    }
                    p {
                        color: $txt-grey;
                        font-size: 16px;
                        font-weight: 200;
                        // padding-bottom: 20px;
                    }
                    a {
                        display: block;
                        padding: 20px 0;
                        padding-bottom: 13px;
                        background-color: $blue;
                        color: white;
                        text-align: center;
                        font-size: 20px;
                        margin-bottom: 20px;
                        margin-top: 20px;
                    }
                }
                @include breakpoint ($medium) {
                    padding: 40px 0;      
                    max-width: 1024px;  
                    @include clearfix;
                    h1 {
                        min-height: 71px;
                    }
                    .post {
                        padding-bottom: 0;
                        margin-bottom: 40px;
                        width: 47%;
                        float: left;
                        margin-right: 3%;
                        a {
                            margin-bottom: 0;
                            width: 130px;
                            font-size: 18px;
                        }
                        &:nth-child(even) {
                            margin-right: 0;
                            margin-left: 3%;
                        }
                    }
                }
            }
        }
    }
}