#read-more-section {
    .read-more-wrapper {
        .read-more-container {
            .read-more {
                @include clearfix;
                .link-square {
                    text-align: center;
                    padding: 100px 0;
                    @include breakpoint ($large) {
                        width: 33.3333%;
                        float: left;
                    }
                    h1 {
                        font-size: 2.5em;
                        color: white;
                        margin: 0;
                        border-bottom: 10px solid $green;
                        display: inline-block;
                        text-shadow: 1px 1px 2px rgba(0,0,0,.2);
                        font-weight: 800;
                    }
                    h4 {
                        font-size: 20px;
                        letter-spacing: 6px;
                        text-shadow: 1px 1px 2px rgba(0,0,0,.2);
                        color: white;
                        font-weight: 200;
                        margin: 20px auto 0;
                        text-transform: uppercase;
                    }
                    @include breakpoint ($large) {
                        &:hover {
                            h1 {
                                transition: 0.2s ease;
                                border-bottom: 10px solid $super-light-blue;
                            }
                        }
                    }
                    @media screen and (min-width: 1200px) {
                        padding: 150px 0;
                    }
                }
                .square-1 {
                    background: url(../img/bg-infotech.jpg) 50% 50% no-repeat;
                    background-size: cover;
                }
                .square-2 {
                    background: url(../img/bg-office-pro.jpg) 50% 0% no-repeat;
                    background-size: cover;
                }
                .square-3 {
                    background: url(../img/bg-industrial.jpg) 50% 15% no-repeat;
                    background-size: cover;
                }
            }
        }
    }
}