#internal-banner-section {
    .internal-banner-wrapper {
        .internal-banner-container {
            background: url(../img/bg-internal.jpg) 60% 0% no-repeat;
            background-size: cover;
            background-attachment: fixed;
            .internal-banner {
                h1 {
                    text-align: center;
                    color: white;
                    text-shadow: 1px 1px 2px rgba(0,0,0,.5);
                    padding: 50px 0;
                    padding-bottom: 45px;
                    line-height: 1.3;
                    margin: 0;
                    font-weight: 800;
                    @include breakpoint ($large) {
                        font-size: 64px;
                    }
                }
                @include breakpoint ($large) {
                    padding: 50px 0;
                    padding-bottom: 40px;
                }
            }
        }
    }
}
#internal-body-section {
    .internal-body-wrapper {
        .internal-body-container {
            width: 85%;
            margin: 0 auto;
            padding: 40px 0;
            max-width: 900px;
            .internal-body {
                h1 {
                    color: $blue;
                    line-height: 1.2;
                    font-size: 1.5em;
                    margin-bottom: 20px;
                }
                p {
                    color: $txt-grey;
                    font-size: 16px;
                    font-weight: 100;
                    padding-bottom: 20px;
                }
                h2 {
                    margin: 0;
                    font-size: 20px;
                    color: #a9df94;
                    font-weight: 400;
                    padding-bottom: 10px;
                }
                ul {
                    background-color: rgba(72,108,144,.05);
                    padding: 30px 35px;
                    padding-bottom: 23px;
                    margin: 5px 0 30px 0;
                    li {
                        color: $blue;
                        font-size: 14px;
                        list-style-type: none;
                        padding-bottom: 10px;
                        &:last-child {
                            padding-bottom: 0;
                        }
                    }
                    li:before {
                        content: '';
                        display: inline-block;
                        background: url(../img/ul-bullet.png) 50% 50% no-repeat;
                        width: 10px;
                        height: 10px;
                        margin-right: 10px;
                        background-size: contain;
                    }
                }
                .apply-block {
                    width: 90%;
                    margin: 0 auto;
                    text-align: center;
                    background-color: #f1f1f1;
                    padding: 30px 0;
                    margin-bottom: 20px;
                    display: block;
                    h4 {
                        margin-top: 0;
                        font-size: 18px;
                    }
                    a {
                        width: 80%;
                        margin: 0 auto;

                    }
                    @include breakpoint ($large) {
                        width: 32%;
                        float: left;
                        margin-right: 1.3334%;
                        &:last-child {
                            margin-right: 0;
                        }
                    }
                }
            }
            @include breakpoint ($large) {
                width: 65%;
                max-width: 1024px;
                padding: 75px 0;
                max-width: 900px;
                .internal-body {
                    h1 {
                        font-size: 1.8em;
                    }
                    ul {
                        padding: 40px 45px;
                        padding-bottom: 35px;
                    }
                }
            }
        }
    }
}
#case-study-banner-section {
    .case-study-banner-wrapper {
            background-color: $blue;
            padding: 50px 0;
            padding-bottom: 55px;
        .case-study-banner-container {
            img {
                display: none;
            }
            .case-study-banner {
                width: 85%;
                margin: 0 auto;
                text-align: center;
                h1 {
                    color: white;
                    line-height: 1.2;
                    font-size: 1.5em;
                    margin-bottom: 15px;
                }
                p {
                    color: white;
                    font-size: 15px;
                    font-weight: 100;
                    padding-bottom: 25px;
                }
                h2 {
                    margin: 0;
                    font-size: 20px;
                    color: #a9df94;
                    font-weight: 200;
                    padding-bottom: 15px;
                }
                a {
                    display: block;
                    background-color: $green;
                    color: $blue;
                    padding: 20px 0;
                    padding-bottom: 13px;
                    font-size: 18px;
                    font-weight: 600;
                    width: 300px;
                }
                @include breakpoint ($large) {
                    width: 58%;
                    text-align: left;
                    float: right;
                    padding-top: 50px;
                    a {
                        text-align: center;
                    }
                }
            }
            @include breakpoint ($large) {
                @include clearfix;
                .case-study-img {
                    float: left;
                    width: 38%;
                    img {
                        display: block;
                        border-radius: 50%;
                    } 
                }
                width: 85%;
                max-width: 1024px;
                margin: 0 auto;
            }
        }
    }
}
#reviews-section {
    .reviews-wrapper {
        .reviews-container {
            .reviews {
                width: 85%;
                margin: 0 auto;
                max-width: 1024px;
                padding: 40px 0;
                text-align: center;
                h1 {
                    color: $blue;
                    line-height: 1.2;
                    font-size: 1.5em;
                    margin-bottom: 15px;
                }
                .block-text {
                    color: $light-blue;
                }
                .review-content {
                    .review-img {
                        display: none;
                    }
                    p {
                        color: $txt-grey;
                        font-size: 15px;
                        font-weight: 100;
                        padding-bottom: 25px;
                    }
                    .reviewer-name {
                        li {
                            color: $blue;
                            font-size: 14px;
                            padding: 0;
                            padding-top: 30px;
                            border-top: 5px solid $green;
                            list-style-type: none;
                        }
                        li:before {
                            content: '';
                            display: inline-block;
                            background: url(../img/ul-bullet.png) 50% 50% no-repeat;
                            width: 10px;
                            height: 10px;
                            margin-right: 10px;
                            background-size: contain;
                        }
                    }
                }
                @include breakpoint ($large) {
                    @include clearfix;
                    h1 {
                        font-size: 1.8em;
                    }
                    .block-text {
                        width: 35%;
                        float: left;
                        h2 {
                            font-size: 2em;
                            margin: 0;
                            text-align: left;
                            line-height: 1.3;
                        }
                    }
                    .review-content {
                        width: 62%;
                        float: right;
                        border-left: 1px solid $txt-grey;
                        padding-left: 30px;
                        text-align: left;
                        .review-img {
                            display: block;
                            margin-bottom: 20px;
                            img {
                                max-height: 180px;
                            }
                        }
                    }
                }
            }
        }
    }
}


#contact-us-page-form {
    ul {
        background-color: white !important;
        padding: 0 !important;
    }
    li {
        list-style-type: none;
        &:before {
            opacity: 0;
            height: 0;
            width: 0;
            margin: 0;
        }
        label {
            display: none;
        }
    }
    #input_2_1, #input_2_2, #input_2_3, textarea {
        margin: 0;
        padding: 5px;
        padding-top: 10px;
        width: 100%;
        border: 1px solid #d1d1d1;
        font-size: 16px;
    }
    #gform_submit_button_2 {
        padding: 23px 0 15px 0;
        text-align: center;
        margin-top: 20px;
        font-size: 1.5em;
        color: $blue;
        background-color: $green;
        display: block;
        transition: 0.2s ease;
        width: 100%;
        @include breakpoint ($large) {
            &:hover {
                background-color: $blue;
                color: $green;
            }
        }
    }
}

.hungryfeed_feed_title, .hungryfeed_feed_description {
    display: none;
}

.hungryfeed_items {
    .hungryfeed_item {
        padding: 75px;
        background-color: #f1f1f1;
        padding-top: 50px;
        margin-bottom: 30px;
        h3 {
            font-size: 1.6em;
            margin-bottom: 40px;
            @include breakpoint ($large) {
                transition: 0.2s ease;
                &:hover {
                    text-decoration: underline;
                }
            }
        }
        font-size: 1.1em;
        table {
            tbody {
                tr {
                    td {
                        color: #666;
                        border-bottom: 1px solid #c9d7e1;
                    }
                }
            }
        }
        div {
            &:nth-child(3) {
                display: none;
            }
            &:last-child {
                display: none;
            }
        }
    }
}
